@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap");

* {
  padding: 0;
  margin: 0;
}

button {
  padding: 0.5rem 1rem;
  margin: 1rem 0.5rem;
}

body {
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #0066ff;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  width: 100%;
  height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.heart {
  position: absolute;
  z-index: 0;
  opacity: 0;
  animation-name: hearts;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.gallery {
  position: absolute;
  top: 1rem;
  left: 1rem;
  right: 1rem;
  bottom: 1rem;
  z-index: 1;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(5, 1fr);
}

.gallery > div {
  overflow: hidden;
  transition: 0.5s;
  border-radius: 0.25rem;
}

img {
  max-width: 100%;
}

.gallery > div > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hide {
  opacity: 0;
}

.show {
  opacity: 0.9;
}

.card-wrapper {
  width: 100%;
  animation: card 1s ease;
}

.card {
  white-space: pre-wrap;
  background-color: rgba(255, 255, 255, 1);
  padding: 2rem;
  border-radius: 0.25rem;
  box-shadow: 0 15px 20px rgba(0, 102, 255, 0.2);
  color: #0066ff !important;
  font-size: 1.4rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 2px;
}

.card-border {
  padding: 1rem;
  max-width: 450px;
  margin: 0 auto;
  background: linear-gradient(to right, #0066ff, #ff0066);
  background-size: 300%;
  border-radius: 0.5rem;
  animation: border 3s linear infinite;
}

.card-play {
  position: relative;
  padding: 1.5rem;
  background: linear-gradient(45deg, #0066ff, #ff0066);
  border-radius: 50%;
  cursor: pointer;
  border: none;
  box-shadow: 0 5px 10px rgba(0, 102, 255, 0.4);
}

.card-play::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: transparent;
  border: 0.5rem solid transparent;
  border-left: 0.8rem solid #fff;
  margin-left: 0.125rem;
  border-right: none;
}

.card-wrapper,
.card-play {
  position: relative;
  z-index: 2;
}

@keyframes card {
  0% {
    opacity: 0.5;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes border {
  0% {
    background-position: left;
  }
  50% {
    background-position: right;
  }
  100% {
    background-position: left;
  }
}

@keyframes hearts {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(-100px);
  }
}
